<template>
  <div id="home">
    <nav-bar class="home-nav">
      <div slot="center">购物街</div>
    </nav-bar>
    <div class="homeContent" @scroll="contentScroll($event)" ref="homeContent">
      <!-- @contentScroll 就是不能写“()” 如果非要写，要传递$event -->
      <home-swiper :banners="banners"></home-swiper>
      <recommend-view :recommends="recommend"></recommend-view>
      <feature-view />
      <tab-control
        :titles="['流行', '新款', '精选']"
        @tabClick="tabClick"
        ref="tabControl"
      />
      <goods-list :goods="showGoods" />
    </div>
    <back-top @click.native="backTop()" v-show="isShowBackTop" />
    <!-- .native监听组件根元素的原生事件 -->
  </div>
</template>

<script>
// Home子组件
import HomeSwiper from "./childComps/HomeSwiper.vue";
import RecommendView from "./childComps/RecommendView.vue";
import FeatureView from "./childComps/FeatureView.vue";
// 通用组件或业务组件
import NavBar from "components/common/navbar/NavBar.vue";
import TabControl from "components/content/tabControl/TabControl.vue";
import GoodsList from "components/content/goods/GoodsList.vue";
// 网络请求
import { getHomeMultidata, getHomeGoods } from "network/home";
// 工具函数
import { _throttle } from "common/utils";
import { itemListenerMixin, backTopMixin } from "common/mixin";
// toast
export default {
  name: "Home",
  data() {
    return {
      banners: [],
      dKeyword: [],
      keywords: [],
      recommend: [],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop",
      isFixed: false,
    };
  },
  components: {
    NavBar,
    HomeSwiper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
  },
  computed: {
    showGoods() {
      return this.goods[this.currentType].list;
    },
  },
  created() {
    // 1.请求多个数据
    this.getHomeMultidata();
    // 2.请求goods数据
    this.getHomeGoods("pop");
    this.getHomeGoods("new");
    this.getHomeGoods("sell");
    this.loadMore = _throttle(this.loadMore, 5000);
  },
  mounted() {},
  deactivated() {
    this.$bus.$off("itemImageLoad", this.itemImageLoad);
  },
  mixins: [backTopMixin],
  methods: {
    // 网络请求
    getHomeMultidata() {
      getHomeMultidata().then((res) => {
        this.banners = res.data.banner.list;
        this.recommend = res.data.recommend.list;
      });
    },
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page)
        .then((res) => {
          this.goods[type].list.push(...res.data.list);
          this.goods[type].page++;
        })
        .catch((err) => {
          this.$toast.show("请求数据失败");
          console.log(err);
          setTimeout(() => {}, this.DELAY * 2);
        });
    },
    // 事件监听
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
    },
    contentScroll() {
      // 判断backTop是否显示:混入
      // this.listenBackTop(position);
      const homeContentElement = this.$refs.homeContent;
      if (
        homeContentElement.scrollHeight -
          (homeContentElement.scrollTop + homeContentElement.clientHeight) <=
        50
      ) {
        this.loadMore();
      }
      this.isShowBackTop = this.$refs.homeContent.scrollTop >= 50;
    },
    loadMore() {
      this.getHomeGoods(this.currentType);
    },
    backTop() {
      this.$refs.homeContent.scrollTo(0, 0, {
        behavior: "smooth",
      });
    },
  },
};
</script>

<style scoped>
.homeContent {
  height: calc(100vh - 6.59670vh - 7.34632vh);
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.home-nav {
  background-color: var(--color-tint);
  color: #fff;
  font-weight: bold;
}
.tab-control {
  position: sticky;
  top: 0px;
}
</style>